
import Command from '@/interfaces/Command'
import CommandForm from '../components/DeviceTypesComponents/CommandForm.vue'
import { defineComponent } from '@vue/runtime-core'
import MainContent from '../components/MainContent.vue'
import ModalBasicWindow from '../components/modals/ModalBasicWindow.vue'
import CommandService from '@/services/CommandService'
import DeviceTypeService from '@/services/DeviceTypeService'
import ResponseData from '@/services/ResponseData'
import DeviceType from '@/interfaces/DeviceType'
import CommandHelper from '@/interfaces/CommandHelper'
import DeviceForm from '../components/DeviceTypesComponents/DeviceForm.vue'
export default defineComponent({
    name: 'DeviceTypeView',
    data: () => ({
        commands: [] as Array<Command>,
        devices: [] as Array<DeviceType>
    }),
    components: {
        MainContent,
        ModalBasicWindow,
        CommandForm,
        DeviceForm
    },
    created() {
        CommandService.getAllCommands().then( (response: ResponseData<Array<Command>>) => {
            this.commands = response.data;
        }).catch( (err) => {
            console.log(err)
        })

        DeviceTypeService.getAllTypes().then( (response: ResponseData<Array<DeviceType>>) => {
            this.devices = response.data;
        }).catch( (err) => {
            console.log(err)
        })
    },
    methods: {
        addCommand(command: Command){
            let helper= {} as  CommandHelper ;
            helper.id = command.id
            helper.name = command.name;
            helper.commandId = command.commandId;
            helper.fields = JSON.stringify(command.fields);
            CommandService.addCommand(helper).then( (response: ResponseData<CommandHelper>) => {
                let command = {} as Command
                command.name = response.data.name;
                command.commandId = response.data.commandId;
                command.fields = JSON.parse(response.data.fields);
                this.commands.push(command)
            }).catch( (err) => {
                console.log(err)
            })
        },
        editCommand(command: Command){
            let com = this.commands.filter( c => c.id != command.id);
            this.commands = com;
            this.addCommand(command);
        },
        removeCommand(id: string){
            CommandService.deleteCommandById(id).then( () => {
                 let com = this.commands.filter( c => c.id != id);
                this.commands = com;
            }).catch( (err) => {
                console.log(err)
            })
        },
        addDevice(device: DeviceType){
            DeviceTypeService.addDeviceType(device).then( (response: ResponseData<DeviceType>) => {
                this.devices.push(response.data)
            }).catch( (err) => {
                console.log(err)
            })
        },
        editDevice(device: DeviceType){
            let dev = this.devices.filter( d => d.id != device.id);
            this.devices = dev;
            this.addDevice(device);
        },
        removeDevice(id: string){
            DeviceTypeService.deleteTypeById(id).then( () => {
                let dev = this.devices.filter( d => d.id != id);
                this.devices = dev;
            }).catch( (err) => {
                console.log(err)
            })
        }
    }

})
